import React, {useState, useEffect} from 'react';
import {useNavigate } from 'react-router-dom'
import "./Confirmation.scss"
import Header from '../../Header/Header';
import Footer from '../../Footer/Footer';
import Api from "../../Api/Api";

function ConfirmationEmailPage (props) {
  const {isAuthenticated, setIsAuthenticated, menu, icon} = props;
  const navigate = useNavigate()
  const [isPopupAccountOpen, setIsPopupAccountOpen] = useState(false);
  const [errors, setErrors] = useState("");
  useEffect(() => {
    const accessToken = localStorage.getItem('access_token'); 
    if (accessToken) {
      navigate('/');
    }
  }, [navigate]);
  
  function submitForm(event) {

    event.preventDefault(); //    
    const form = event.target; //    
    const formData = new FormData(form); //   FormData
  

    Api.post('api/v1/confirmation/', formData)
      .then(response => {
        localStorage.setItem("access_token", response.data.access);
        localStorage.setItem("refresh_token", response.data.refresh);
        if (response.status === 201) {
          navigate('/')
        }

      })
      .catch((error) => {
        if (error.response.status === 400) {
          //    ,    
         setErrors(" ");
        }
      });
  }
  //     submit 
  const Form = () => {
    return (
      <div className="confirmation-page background">
        <Header menu={menu} icon={icon} isPopupAccountOpen={isPopupAccountOpen} setIsPopupAccountOpen={setIsPopupAccountOpen} isAuthenticated={isAuthenticated} setIsAuthenticated={setIsAuthenticated}/>
        <main className='confirmation-page__content wrapper'>
          <h1 className='confirmation-page__title'></h1>
          <h2 className='confirmation-page__subtitle'> </h2>
          <form className="confirmation-form" onSubmit={submitForm}>
            <label  htmlFor="confirmation-email">       .     </label>
            <input type="text" id="confirmation-email" name="confirmation_token" placeholder='    '/>
            <div className={`error ${errors? "error_active" : ""}`}>{errors}</div> 
            <button type="submit" className='confirmation-form__btn'></button>
          </form>
        </main>
        <Footer menu={menu} icon={icon}/>
      </div>
    )
   }

   return <Form />;
 }
export default ConfirmationEmailPage;